import { setSelectionRange } from '@testing-library/user-event/dist/utils';
import React, { useEffect, useState } from 'react'
import "./style.css";

// get item from localstorage

const getLocData = () => {
  const lists = localStorage.getItem("mytodolist");
  if (lists) {
    return JSON.parse(lists);

  }
  else {
    return [];
  }
};

const Todo = () => {

  const [inputData, setInputData] = useState("");
  const [items, setItems] = useState(getLocData());
  const [isEditItem, setIsEditItem] = useState("");
  const [toggleButton, setToggleButton] = useState(false);


  //add the iem function

  const addItem = () => {
    if (!inputData) {
      alert("Please fill the data")
    } else if (inputData && toggleButton) {
      setItems(
        items.map((curElem) => {
          if (curElem.id === isEditItem) {
            return { ...curElem, name: inputData };
          }
          return curElem;
        })
      );
      setInputData("");
      setIsEditItem(null);
      setToggleButton(false);

    }
    else {
      const myNewInputData = {
        id: new Date().getTime().toString(),
        name: inputData,
      };
      setItems([...items, myNewInputData]);
      setInputData("");
    }
  };

  //edit the items

  const editItem = (index) => {
    const item_todo_edited = items.find((curElem) => {
      return curElem.id === index;
    });

    setInputData(item_todo_edited.name);
    setIsEditItem(index);
    setToggleButton(true);
  };

  // how to delete item section
  const deleteItem = (index) => {
    const updatedItem = items.filter((curElem) => {
      return curElem.id !== index;
    }

    );
    setItems(updatedItem);
  };

  // remove all item section
  const removeAll = () => {
    setItems([]);
  };

  // adding localstorage
  useEffect(() => {
    localStorage.setItem("mytodolist", JSON.stringify(items));
  }, [items]);

  return (
    <>
      <div className="main-div">
        <div className="child-div">
          <figure>
            <img src='./images/todo.svg' />
            <figcaption>Add Your List Here 👏</figcaption>
          </figure>
          <div className="addItems">
            <input type="text" placeholder=" ✍ Add Item" className='form-control' value={inputData} onChange={(e) => setInputData(e.target.value)} />

            {toggleButton ? (<i className="far fa-edit add-btn" onClick={addItem}></i>) : (<i className="fa fa-plus add-btn" onClick={addItem}></i>)}
          </div>

          {/* show our item section */}
          <div className="showItems">

            {items.map((curElem, index) => {
              return (
                <div className="eachItem" key={curElem.id}>
                  <h3>{curElem.name}</h3>
                  <div className="todo-btn">
                    <i className="far fa-edit add-btn" onClick={() => editItem(curElem.id)}></i>
                    <i className="far fa-trash-alt add-btn" onClick={() => deleteItem(curElem.id)}></i>
                  </div>
                </div>
              );
            }

            )}
          </div>

          {/* remove all button section */}

          <div className='showItems'><button className='btn effect04' data-sm-link-text="Remove All" onClick={removeAll}><span>Check List</span></button></div>
        </div>
      </div>
    </>
  )
}

export default Todo
